@import 'variables';
@import 'mixins';
@import 'container';
@import 'segment';

/*General style*/
html {
  background-color: $background-gray;
}
body {
  margin: 0;
  background: url(../img/web/pattern.png);
  height: 100%;
  font-family: 'Roboto Condensed', sans-serif;
}

/*General*/
@media only screen and (max-width: 767px) {
  .responsive.grid {
    flex-direction: column;

    .col {
      width: 100%;
      padding: 0 .5rem 0;
    }
  }
}

.disabled {
  pointer-events: none;
  opacity: .5;
}

.element {
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1.0, 1.0);
  transform: translateZ(0);
  padding: 0;
  border-radius: $radius;
  overflow: hidden;

  img {
    height: auto;
    width: auto;
    max-width: 100%;
  }
  
  &:not(.static) {
    cursor: pointer;
    transition: transform 0.5s ease;
  }

  .title {
    position: absolute;
    bottom: -1px;
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    padding: .5rem 0;
    background-color: $element-gray;
    transition: background-color 0.5s ease;
    border-radius: 0 0 $radius $radius;
  }

  &:not(.static) {
    &:hover {
      transform: scale(1.0125, 1.0125);

      .title {
        background: $blue;
      }
    }
  } 
}

.main.element {
  .title {
    font-size: 2rem;
    padding: .5rem 0;
  }
}

.gallery.element {
  position: relative;

  .button {
      @include flex-vertical();
      @include box-shadow(0, .15rem, 1rem, $dark-shadow);
      position: absolute;
      z-index: 1;
      background-color: $blue;
      height: 3rem;
      width: 3rem;
      border-radius: 1.5rem;
      top: calc(50% - 1.5rem);
      transition: background-color .5s ease;
      cursor: pointer;
      
      &:hover {
          background-color: $magenta;
      }
  }
  
  .prev {
      left: 1.5rem;
      right: auto;
      
      i {
          margin-left: -.25rem;
      }
  }
  
  .next {
      left: auto;
      right: 1.5rem;
      
      i {
          margin-right: -.25rem;
      }
  }

  ul {
    @include flex-horizontal();
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
    margin: 0;
    padding: 0;
    list-style:none;
    justify-content: flex-start;

    li, img {
      flex: 1 0 100%;
      height: 100%;
      width: 100%;
    }
  }
}

.information {
  color: $light-text;
}

.hero-header {
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: fixed;
    top: 0;
    left: 0;
}

/*Main menu*/
#menu {
  @include flex-horizontal();
  position: fixed;
  z-index: 5;
  left: 0;
  width: 100vw;
  height: 4rem;
  justify-content: space-between;
  top: -1rem;
  opacity: 0;

  .spacer {
    width: 0;
  }

  .directory {
    color: $light-text;
    text-decoration: none;
    text-transform: uppercase;
    width: 100%;
    text-align: center;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    height: 100%;
    line-height: 4rem;

    &:hover {
      .hidden {
        top: 35%;
        opacity: 1;
      }

      .visible {
        transform: translateY(20%);
      }
    }

    .hidden {
      position: absolute;
      top: -50%;
      margin-top: -.5em;
      opacity: 0;
    }

    .visible {
      position: relative;
      transform: translateY(0);
      opacity: 1;
    }

    .hidden,
    .visible {
      will-change: transform,opacity;
      width: 100%;
      text-align: center;
      transition: top .35s ease, transform .5s ease, opacity .125s ease;
    }
  }
}

/*Page cover*/
#page-cover {
  position: fixed;
  z-index: 1;
  display: block;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background: url(../img/web/ae.jpg);
}

/*Av logo*/
#av-logo {
  @include flex-vertical();
  position: fixed;
  z-index: 2;
  perspective: 600px !important;
  transition: filter .5s ease;
  z-index: 10;

  img {
    display: block;
    height: 18rem;
    width: 32rem;
    transform: rotateX(90deg);
    align-self:center;
  }

  &:hover {
    filter: brightness(1.5) !important;
  }

  &.reduced {
    filter: brightness(0) invert(1);
    cursor: pointer;
  }
}

/*Page*/
#page-container-wrapper {
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

#page-container {
  height: 100%;
  width: 100%;
  overflow: auto;
  background-size: cover;
  background-position: center center;
  background-size: cover;
}

.page-content {
  display: none;
}

/*About*/
.resume.container {
  @include flex-horizontal();

  .sidebar {
    background: $resume-sidebar-gray;
    width: 30%;
    border-radius: $radius;
  }

  .content {
    width: 100%;
  }
}
